@keyframes blink {
 0% {
   opacity: 1;
 } 

 50% {
   opacity: 0;
 }

 100% {
   opacity: 1;
 }
}

.App {
  text-align: center;
  margin: 0;
  padding: 0;
}

.App-logo {
  height: auto;
  pointer-events: none;
  width: 98vw;
}

#left {
  margin-top: 1em;
}

.App-header {
  background-color: #282c34;
  min-height: 104vh;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: 'Times New Roman', Times, serif;
  display: flex;
  justify-content: center;
  align-items: flex-start; 
  flex-flow: column;
}

.App-link {
  color: #61dafb;
}

#list {
  text-align: left;
}

.li-label {
  margin-right: 0.25em;
}

.bottom-arrow {
  height: auto;
  position: absolute;
  width: 4em;
  bottom: 0%;
  right: 0%;
  animation: blink 2s infinite;
}
